import React, { useState, useEffect } from "react";
import { TextField, CircularProgress } from "@mui/material";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import Button from "@mui/material/Button";
import SearchIcon from "@mui/icons-material/Search";
import { Tab, Tabs } from "@mui/material";
import axios from "axios";
import CustomPagination from "../../Component/pagination";
import SingleComponent from "../../Component/SingleComponent/SingleComponent";

const darkTheme = createTheme({
  palette: {
    mode: "dark",
    primary: {
      main: "#fff",
    },
  },
});

const Search = () => {
  const [type, setType] = useState(0);
  const [page, setPage] = useState(1);
  const [searchText, setSearchText] = useState("");
  const [content, setContent] = useState([]);
  const [numOfPages, setNumOfPages] = useState(0);
  const [loading, setLoading] = useState(false);

  const fetchSearch = async () => {
    if (searchText.trim() === '') return;

    setLoading(true);
    try {
      const { data } = await axios.get(
        `https://api.themoviedb.org/3/search/${type ? "tv" : "movie"}?api_key=${process.env.REACT_APP_API_KEY}&language=en-US&query=${searchText}&page=${page}`
      );

      setContent(data.results);
      setNumOfPages(data.total_pages);
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    window.scroll(0, 0);
    fetchSearch();
    // eslint-disable-next-line
  }, [type, page, searchText]);

  const handleSearch = () => {
    setPage(1);
    fetchSearch();
  };

  return (
    <div>
      <ThemeProvider theme={darkTheme}>
        <div className="search" style={{ display: "flex", margin: "15px 0" }}>
          <TextField
            style={{ flex: 1 }}
            className="searchBox"
            label="Search"
            variant="filled"
            onChange={(e) => setSearchText(e.target.value)}
          />
          <Button variant="contained" style={{ marginLeft: 10 }} onClick={handleSearch}>
            <SearchIcon fontSize="large" />
          </Button>
        </div>
      </ThemeProvider>
      <Tabs
        value={type}
        indicatorColor="primary"
        textColor="primary"
        onChange={(event, newValue) => {
          setType(newValue);
          setPage(1);
        }}
        style={{ paddingBottom: 5 }}
      >
        <Tab style={{ width: "50%", color: "#ffffff" }} label="Search Movies" />
        <Tab style={{ width: "50%", color: "#ffffff" }} label="Search TV Series" />
      </Tabs>
      <div className="trending">
        {loading ? (
          <CircularProgress /> // Show loading indicator while fetching data
        ) : content.length === 0 && searchText !== "" ? (
          <h2>{type ? "No Series Found" : "No Movies Found"}</h2>
        ) : (
          content.map((c) => (
            <SingleComponent
              key={c.id}
              id={c.id}
              poster={c.poster_path}
              title={c.title || c.name}
              date={c.first_air_date || c.release_date}
              media_type={type ? "tv" : "movie"}
              vote_average={c.vote_average}
            />
          ))
        )}
      </div>
      {numOfPages > 1 && <CustomPagination setPage={setPage} totalPage={numOfPages} />}
    </div>
  );
};

export default Search;
